/* Spacing between components (according to application guidestyle)
    Large - 24px
    Medium - 12px
    Small - 6px
*/
/* breakpoints */
html,
body {
  height: 100%;
  max-width: 100%;
}
body {
  display: flex;
  flex-direction: column;
}
#root {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100%;
  height: 100%;
}
::selection {
  background: #0c8cb1 !important;
  text-shadow: none;
  color: #fff !important;
}
.appHeader {
  background-color: black;
  color: white;
  width: 100%;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  min-height: 85px;
}
.appHeader .category {
  padding: 0.5em;
  width: 200px;
  font-size: 24px;
  color: black;
  text-decoration: inherit;
  background-color: white;
  position: relative;
}
.appHeader .category .fa-tag {
  font-size: 2em;
}
.appHeader .category span {
  margin-left: 1em;
  top: 20px;
  position: absolute;
}
.appHeader .separator {
  border-right: 42.5px solid #000;
  border-top: 85px solid #fff;
}
.appHeader .testlist {
  right: 2em;
  position: absolute;
  color: black;
}
.subHeader {
  flex: 1;
}
.subHeader .subHeaderItem {
  display: inline-flex;
  padding: 1em;
  margin-right: 1em;
  font-weight: normal;
  text-shadow: none;
}
.subHeader .subHeaderItem .fa {
  font-size: 2em;
  margin-right: 0.4em;
  width: 1em;
}
.subHeader .subHeaderItem .desc {
  display: inline-block;
}
.subHeader .subHeaderItem .desc .subdesc {
  font-size: 0.9em;
}
.subHeader .subHeaderItem .desc .maindesc {
  font-size: 1.2em;
}
.mainSection {
  flex: 1;
  display: flex;
  flex-direction: column;
  /*min-height: 100vmin;*/
  overflow-y: auto;
  background-color: #f4f6f9;
  border: 1px solid #d8dde6;
}
.mainSection > div {
  background-color: #f4f6f9;
}
.mainSection .mainBodySection {
  flex: 1 1 0;
}
.mainSection .slds-page-header__title {
  max-width: 800px;
  font-size: 1.6rem;
  line-height: 1.6rem;
  color: #16325c;
}
.overview {
  flex: 1;
}
.overview.unpinned {
  margin-left: 24px;
  position: relative;
}
.search {
  width: 100%;
  border: 1px solid #aaa;
  padding: 0.5em;
  font-size: 1.8em;
  display: flex;
}
.search input {
  flex: 1;
  border: 0;
  margin-left: 1em;
  margin-right: 1em;
  font-size: 0.8em;
}
.tabs {
  margin-top: 0.2em;
}
.tabs .tabHeader {
  width: 10em;
  padding: 0.3em;
  border: 1px solid #aaa;
  text-align: center;
  display: inline-block;
}
.tabs .tabHeader.active {
  background-color: #005fb2;
  color: #FFF;
}
.grid table {
  width: 100%;
  margin-top: 1em;
}
.grid .cellHeader {
  border: 1px solid #aaa;
  background: #dedede;
  padding: 0.5em;
}
.grid .rowHeader {
  border: 1px solid #aaa;
  background: #dedede;
  padding: 0.3em;
  font-weight: bold;
}
.grid .cell {
  text-align: right;
  border: 1px dotted #aaa;
  padding-right: 0.2em;
}
.promotion-toolbar {
  height: 55px;
  padding: 10px 12px;
}
.promotion-toolbar .slds-button {
  height: 34px;
  margin-bottom: 2px;
}
.promotion-toolbar .slds-button--neutral {
  background-color: #f4f6f9;
}
.promotion-toolbar .slds-dropdown__item > a {
  white-space: normal;
}
.promotion-toolbar .ui-attachment__button {
  height: 54px;
  width: 49px;
  margin: -10px -12px 0 12px;
  padding: 0;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  border: none;
  border-left: 1px solid #d8dde6;
}
.promotion-toolbar .ui-attachment__button:focus {
  box-shadow: none;
}
.promotion-toolbar .ui-attachment__button .slds-button__icon {
  fill: #54698d;
}
.promotion-toolbar .ui-attachment__button:hover .slds-button__icon {
  fill: #005fb2;
}
.promotion-toolbar .ui-attachment__button.slds-button--brand .slds-button__icon {
  fill: #FFF;
}
.promotion-toolbar .ui-attachment__button.slds-button--brand[disabled] .slds-button__icon {
  fill: #a7b8d1;
}
.promotion-toolbar .ui-attachment__button.slds-button--neutral[disabled] .slds-button__icon {
  fill: #a7b8d1;
}
.menuItemLabel {
  position: relative;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 0.5rem 0.75rem;
  color: #4f6a92;
  white-space: nowrap;
  text-transform: uppercase;
}
.toggleButton {
  display: inline-block;
  /* padding-top: 5px; */
  /* padding-bottom: 5px; */
  margin-right: 2em;
  font-size: 20px;
  border: 2px solid gray;
  border-radius: 3px;
  line-height: 36px;
  text-shadow: none;
}
.toggleButton a {
  padding: 9px;
  border-left: 2px solid gray;
}
.toggleButton a:first-child {
  border-left: none;
}
.toggleButton a.selected {
  background-color: gray;
  color: white;
}
.button {
  border: 2px solid gray;
  min-width: 90px;
  display: inline-block;
  padding-left: 1em;
  line-height: 36px;
  border-radius: 3px;
}
.button i {
  margin-right: 1em;
}
.view-switch {
  width: 34px;
  height: 34px;
  padding: 8px;
  line-height: initial;
}
.view-switch.slds-button-space-left {
  margin-left: 0;
}
.view-switch.slds-button--brand {
  color: #ffffff;
  background-color: #0070d2;
}
.view-switch.slds-button--neutral {
  color: #54698d;
  background-color: #ffffff;
}
.view-switch > svg {
  width: 16px;
  height: 16px;
}
.panel-enter {
  opacity: 0.01;
}
.panel-enter.panel-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.panel-leave {
  opacity: 1;
}
.panel-leave.panel-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}
.w100,
.w100 > button {
  width: 100%;
}
.column-header {
  white-space: normal;
  vertical-align: middle;
}
.edit-fund {
  background-color: white;
  padding: 0.75rem;
}
.edit-fund:before {
  position: absolute;
  top: 312px;
  left: 105px;
  display: inline-block;
  border-right: 10px solid transparent;
  border-top: 10px solid #ccc;
  border-left: 10px solid transparent;
  border-top-color: rgba(0, 0, 0, 0.2);
  content: '';
}
.edit-fund:after {
  position: absolute;
  top: 312px;
  left: 105px;
  display: inline-block;
  border-right: 10px solid transparent;
  border-top: 10px solid #ffffff;
  border-left: 10px solid transparent;
  content: '';
}
.slds-input[disabled] {
  border-width: 0;
  border-bottom: 1px solid #d8dde6;
  background-color: #ffffff;
  cursor: default;
}
.slds-input[disabled] + svg {
  display: none;
}
.slds-dropdown__item {
  margin-left: 0;
}
.side-panel {
  width: 0;
  border-left: 1px solid #cfd7e6;
  background-color: white;
  max-width: 321px;
}
.side-panel .side-panel-header {
  border-bottom: 1px solid #cfd7e6;
  color: #16325c;
}
.side-panel .side-panel-body {
  padding: 20px 15px 10px 10px;
}
.pill-panel {
  border: 1px solid #cfd7e6;
  border-radius: 3px;
  cursor: pointer;
}
.pill-panel .close-button {
  position: relative;
  top: -1em;
}
.pill-panel.tpm-open-pill {
  background-color: #faffbd;
}
.pill-panel:hover {
  background-color: #f4f6f9;
}
.multiselection .slds-dropdown--menu,
.multiselection .slds-button,
.multiselection .slds-dropdown-trigger {
  width: 100%;
}
.multiselection .slds-button {
  margin-left: 0;
  padding-right: 2rem;
  width: 15rem;
  color: #16325c;
  text-align: left;
  width: 100%;
}
.multiselection .slds-button .slds-button__icon {
  width: 0.75rem;
  height: 0.75rem;
  position: absolute;
  right: 1rem;
  top: 50%;
  margin-top: -0.375rem;
  fill: #54698d;
}
.slds-form-element__label {
  font-size: 14px;
}
.slds-picklist button.slds-picklist__label {
  width: 100%;
}
.slds-popover__body {
  background-color: #ffffff;
}
.slds-popover.slds-popover--panel::before {
  background-color: #ffffff;
  z-index: 9999;
}
.required {
  color: #c23934;
}
