.slds-button__icon {
  position: relative;
  top: -1px;
}
.slds-picklist__label {
  font-size: .8125rem;
  font-weight: normal;
  padding-left: 0.75rem;
}
.slds-button {
  font-weight: normal;
}
.tile .slds-form-element__label {
  margin-bottom: 0;
  margin-right: 0;
  font-size: 12px;
}
.tile .slds-form-element__label.slds-form-element__label--title {
  font-size: 14px;
}
@media only screen and (max-width: 1023px) {
  .tile .slds-size--1-of-1 {
    width: 100%;
    margin-bottom: 24px;
  }
  .tile .slds-size--1-of-1 .slds-wrap .slds-size--1-of-1 {
    margin-bottom: 0;
  }
  .tile .slds-size--1-of-1:last-child {
    margin-bottom: 0;
  }
  .tile .slds-size--1-of-2 {
    width: 50%;
  }
  .tile .slds-size--1-of-3 {
    width: 33.3%;
  }
  .tile .slds-col--no-padded,
  .tile .slds-col--padded-left {
    padding: 0;
  }
}
@media only screen and (min-width: 1024px) and (max-width: 1279px) {
  .tile .slds-medium-size--1-of-1 {
    width: 100%;
    margin-bottom: 12px;
  }
  .tile .slds-medium-size--1-of-2 {
    width: 50%;
    margin-bottom: 24px;
  }
  .tile .slds-medium-size--1-of-2:last-child {
    margin-bottom: 0;
  }
  .tile .slds-medium-size--1-of-2 .slds-wrap .slds-medium-size--1-of-2 {
    margin-bottom: 0;
  }
  .tile .slds-medium-size--1-of-2 .slds-wrap .slds-medium-size--1-of-2 + .slds-medium-size--1-of-2 {
    padding-left: 24px;
  }
  .tile .slds-medium-size--1-of-2 .slds-wrap .slds-medium-size--1-of-2 + .slds-medium-size--1-of-2 .slds-checkbox {
    margin-top: 40px;
  }
  .tile .slds-medium-size--1-of-3 {
    width: 33.3%;
  }
  .tile .slds-col--padded-left {
    padding-left: 24px;
  }
  .tile .slds-col--no-padded {
    padding: 0;
  }
}
@media only screen and (min-width: 1280px) {
  .tile .slds-large-size--1-of-1 {
    width: 100%;
  }
  .tile .slds-large-size--1-of-2 {
    width: 50%;
  }
  .tile .slds-large-size--1-of-3 {
    width: 33.3%;
  }
  .tile .slds-col--padded-left {
    padding-left: 24px;
  }
}
.ui-attachments {
  min-height: 80px;
}
.ui-attachments .ui-spinner_container {
  height: 80px;
}
.ui-attachments .ui-file-selector__text,
.ui-attachments .slds-text-link {
  margin: 0;
  text-decoration: none;
  cursor: pointer;
}
.ui-attachments .ui-file-selector__text:hover,
.ui-attachments .slds-text-link:hover {
