  align-items: center;
  display: flex;
  opacity: 0;
  z-index: 20;
  overflow: hidden;
  background: rgba(0, 112, 210, 0.8);
  transition: all .3s;
}
.ui-attachments .ui-attachments__view .ui-attachment__listItem .ui-overlay svg {
  width: 24px;
  height: 24px;
