}
.ui-attachments .ui-attachments__view .slds-link {
  font-size: 12px;
  color: #0070d2;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.1s linear;
}
svg + .ui-attachments .ui-attachments__view .slds-link,
img + .ui-attachments .ui-attachments__view .slds-link {
  margin-left: 6px;
}
.ui-attachments .ui-attachments__view .slds-link:hover {
